             &.act {\n                background-color: @white;\n              }\n              &_hint {\n                opacity: 0;\n                visibility: hidden;\n                display: inline-block;\n                position: absolute;\n                left: 0;\n                text-align: center;\n                width: 100%;\n                top: 0;\n                font-weight: bold;\n                transition: .3s ease;\n                color: white;\n              }\n\n              &:hover {\n                .gift_item_hint {\n                  visibility: visible;\n                  opacity: 1;\n                  top: -20px;\n                }\n              }\n            }\n          }\n        }\n      }\n    }\n\n    & .bns_overlay_iner {\n      width: 500px !important;\n    }\n\n    .status_block {\n      width: 30%;\n      display: inline-block;\n    }\n\n    @media screen and (max-width: 650px) {\n      .status_block {\n        width: 100%;\n      }\n\n      .bon_profile_stat .bps_left {\n        text-align: left;\n      }\n    }\n\n    //media queries\n    @media @desktop-xl {\n      .bon_profile_info {\n        .bon_profile_stat {\n          .bps_right {\n            width: 100%;\n            margin-top: 30px;\n            margin-right: 0px;\n          }\n        }\n      }\n    }\n\n    @media @desktop {\n      .progress_line_main {\n\n        .progress_text {\n          border: none !important;\n\n          &:before {\n            content: '';\n            width: 1px;\n            background: white;\n            right: 0;\n            top: 0;\n            position: absolute;\n            height: 17px;\n            display: block;\n          }\n\n          span {\n\n            position: relative;\n            left: 50%;\n            border-radius: 5px !important;\n\n          }\n\n        }\n\n      }\n    }\n\n    .profile_mobile() {\n\n      .bon_profile_info {\n        width: 100%;\n        .bon_profile_right {\n          width: 265px;\n        }\n        .bon_profile_stat {\n          .bps_right {\n            float: left;\n            width: 100%;\n            margin-top: 30px;\n            margin-bottom: 12px;\n            .progress_line_main {\n              float: left;\n              width: 95%;\n            }\n          }\n        }\n      }\n    }\n\n    @media @tablet {\n      .profile_mobile();\n    }\n\n    @media @mobile {\n      .profile_mobile();\n    }\n\n  }\n\n  //history section\n  .bns_hist_table {\n    float: left;\n    width: 100%;\n    margin-top: 12px;\n    td {\n      vertical-align: text-top;\n      padding: 5px 11px;\n    }\n    td:nth-child(1) {\n      color: @main;\n      font-size: 13px;\n      line-height: 19px;\n      padding-right: 0px;\n      padding-left: 0px;\n      white-space: nowrap;\n    }\n    td:nth-child(2) {\n      color: @black;\n      font-size: 12px;\n      font-weight: 200;\n      line-height: 19px;\n      position: relative;\n      padding-left: 0px;\n      width: 570px;\n      &::after {\n        position: absolute;\n        left: 0px;\n        width: 100%;\n        border-top: 1px dotted @main_dark;\n        top: 14px;\n        content: '';\n        display: block;\n      }\n      span {\n        display: block;\n        position: relative;\n        z-index: 1;\n        font-size: 13px;\n        color: @main_darker;\n        b {\n          background-color: @white;\n          padding-right: 15px;\n          padding-left: 11px;\n          font-weight: 200;\n        }\n      }\n      span:first-child {\n        color: @black;\n      }\n    }\n    td:nth-child(3) {\n      color: @main_dark;\n      font-size: 14px;\n      font-weight: bold;\n      text-align: right;\n      line-height: 19px;\n      span {\n        display: block;\n        white-space: nowrap;\n        font-size: 13px;\n      }\n    }\n  }\n\n  .bns_hist_pager {\n    float: left;\n    font-size: 13px;\n    a {\n      text-decoration: none;\n      color: rgb(172, 172, 171);\n      ma