sor: pointer;\n\n        span {\n          white-space: normal;\n          float: left;\n          color: @main_darker;\n          -webkit-transition: all 300ms ease;\n          -moz-transition: all 300ms ease;\n          -ms-transition: all 300ms ease;\n          -o-transition: all 300ms ease;\n          transition: all 300ms ease;\n        }\n\n        .badge_name {\n          font-size: 16px;\n          width: 100%;\n          text-align: center;\n          margin-top: 10px;\n        }\n        .bon_tem_info {\n          font-size: 14px;\n          opacity: 0.5;\n          font-weight: 300;\n          position: absolute;\n          left: 0px;\n          bottom: 37px;\n        }\n        .badge_pic {\n          width: 100%;\n          max-height: @badge_width;\n          overflow: hidden;\n          img {\n            width: 100%;\n            height: auto;\n          }\n        }\n        a {\n          position: absolute;\n          bottom: 37px;\n          left: 50%;\n          width: 160px;\n          line-height: 35px;\n          text-decoration: none;\n          color: @white;\n          font-size: 14px;\n          font-weight: 500;\n          margin-left: -80px;\n          background-color: @main;\n          border-bottom: 1px solid @black;\n          text-shadow: 0 0 1px @black;\n          opacity: 0;\n          -webkit-transition: all 300ms ease;\n          -moz-transition: all 300ms ease;\n          -ms-transition: all 300ms ease;\n          -o-transition: all 300ms ease;\n          transition: all 300ms ease;\n        }\n      }\n\n      .badge_arrow {\n\n        position: absolute;\n        left: 100%;\n        top: @badge_width/2-10;\n        width: 60px;\n        height: 20px;\n        background-position: center left;\n        background-size: 100% 100%;\n        background-repeat: no-repeat;\n\n      }\n\n      &.last {\n        .badge_arrow {\n          display: none;\n        }\n      }\n\n    }\n\n  }\n\n  //selected badge modal\n  .modal_badge_